


















{ Query } = require "~plugins/memory-record"
BrowserValue = require "~plugins/browser-value"

q = new BrowserValue
q.query
  tag_id:  "all"

module.exports =
  default:
    watch: q.watch ->
    data: ->
      q.data @

    computed:
      name_blanks: ->
        Query.faces.name_blank()

      name_counts: ->
        Query.faces.name_head(@tag_id)




